import * as Yup from 'yup';
import { useState, } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { capitalCase } from 'change-case';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack, Typography } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFSelect, RHFSwitch } from '../../components/hook-form';

import Scrollbar from '../../components/Scrollbar';
import UserSearch from './UserSearch';
import HabukhanUser from './HabukhanUser';
// _mock
import { UserType } from '../../_mock';
// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------

export default function HabukhanUpgradeUser() {
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
    role: Yup.string().required('Account Role Required'),
  });

  const [userUsername, SetUserUsername] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [isSearchFocused, setSearchFocused] = useState(false);
  const displayResults = userUsername && isSearchFocused;
  const AccessToken = window.localStorage.getItem('accessToken');

  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });

  const {
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/credit/upgradeuser/upgrade/${AccessToken}/system/by/system`, {
        user_username: userUsername,
        role: data.role,
        isnotif: data.isNotif
      })
      swal('success', `You have Successfully upgrade/downgrade ${capitalCase(userUsername)}  to '${data.role}' User`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };
  const handleSearchFocus = () => {
    setSearchFocused(true);
  };
  const handleChangeSearch = async (event) => {
    const { value } = event.target;
    SetUserUsername(value);
    if (value) {
      try {
        const response = await axios.post(`/api/filter/user/details/admin/by/habukhan/${AccessToken}/secure/react`, {
          username: value
        });
        setSearchResults(response.data.user);
      } catch (error) {
        console.log(error)
      }
    } else {
      setSearchResults([]);
    }

  };
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
              <UserSearch
                query={userUsername}
                onChange={handleChangeSearch}
                onFocus={handleSearchFocus}
              />
              {displayResults &&
                <Scrollbar>
                  <HabukhanUser query={userUsername} results={searchResults} onSelectContact={(e) => {
                    SetUserUsername(e.username); setSearchFocused(false)
                  }} />
                </Scrollbar>}
              <RHFSelect name="role" label="Account Role" placeholder="Account Role">
                <option value="" />
                {UserType?.map((option) => (
                  <option key={option?.code} value={option?.label}>
                    {option?.label}
                  </option>
                ))}
              </RHFSelect>
              <RHFSwitch
                name="isNotif"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Email Notification
                    </Typography>
                  </>
                }

                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
